import React from 'react'
import Togglable from './Togglable'
import { useSelector } from 'react-redux'
import Notification from './Notification'
import PastApodsForm from './PastApodsForm'

// eslint-disable-next-line react/prop-types
const ImageDesc = ({ imageDesc }) => {
  return (
    <div className='apod-desc'>
      {imageDesc}
    </div>
  )
}

const Apod = () => {
  const apod = useSelector(state => state.apod)

  if (apod === undefined) {
    console.log(apod)
    return null
  }

  const copyright = (apod.copyright === undefined) ? 'Copyright: Public domain' : `(c) ${apod.copyright}`

  if (apod !== undefined) {
    return (
      <div className='content'>
        <h2>Astronomy Picture of the Day</h2>
        <h3>{apod.date}</h3>
        <div key={apod.url}>
          <div className='apodImg'>

            <img src={apod.url} alt={apod.title} />
            <br />
            <b>{copyright} </b>
            <br />
            <br />
          </div>
          <Togglable toggLabel='See description'>
            <b>Description:</b>
            <ImageDesc imageDesc={apod.explanation} />
          </Togglable> </div>
        <div className='previous-apod'>
          <PastApodsForm />
          <div className='notif-div'>
            <Notification />
          </div>
        </div>
        <div className='external-link'>
          All material (c) Nasa.
          <br />
          <a href='https://apod.nasa.gov/apod/' target="_blank" rel="noopener noreferrer">APOD on Nasa homepage</a></div>
        <div>
        </div>
      </div>
    )
  }
}


export default Apod