import searchService from '../services/search'

const initialState = {
  res: [],
  img: null,
  errored: false
}

export const imgError = () => {
  console.log('errored')
  return async dispatch => {
    dispatch({
      type: 'SEARCH_ERROR',
    })
  }
}

export const performSearch = (keyword) => {
  return async dispatch => {
    const res = await searchService.searchImg(keyword)
    dispatch({
      type: 'SEARCH_RESULTS',
      data: {
        res,
        img: null,
        errored: false
      }
    })
  }
}

export const viewImage = (key, alt) => {
  return async dispatch => {
    const imgArray = await searchService.getImg(key)
    console.log('imgArray: ', imgArray)
    const img = imgArray[0]
    dispatch({
      type: 'SET_IMG',
      data: {
        img,
        alt,
        errored: false
      }
    })
  }
}

export const initSearch = () => {
  return async dispatch => {
    dispatch({
      type: 'INIT_SEARCH',
      data: {
        res: [],
        img: null,
        errored: false
      }
    })
  }
}

const searchReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SEARCH_RESULTS':
      return action.data
    case 'INIT_SEARCH':
      return action.data
    case 'SEARCH_ERROR':
      return {
        ...state,
        errored: true,
      }
    case 'SET_IMG':
      return {
        ...state,
        img: action.data.img,
        alt: action.data.alt,
        errored: action.data.errored
      }
    default:
      return state
  }


}


export default searchReducer